<template>
    <div class="flex-information-tracing-information">
        <div
            style="
                background-color: #fff;
                margin-bottom: 10px;
                padding: 12px 24px;
                box-sizing: border-box;
                border-radius: 4px;
            "
        >
            <a-form :form="form" layout="vertical">
                <a-row :gutter="24">
                    <a-col :span="4">
                        <a-form-item label="追溯单号">
                            <a-input
                                placeholder="请输入追溯单号"
                                v-model="form.traceCode"
                            ></a-input>
                        </a-form-item>
                    </a-col>
                    <a-col :span="4">
                        <a-form-item label="产品名称">
                            <a-input
                                placeholder="请输入产品名称"
                                v-model="form.productName"
                            ></a-input>
                        </a-form-item>
                    </a-col>
                    <a-col :span="4">
                        <a-form-item label="批次号">
                            <a-input
                                placeholder="请输入批次号"
                                v-model="form.batchNo"
                            ></a-input>
                        </a-form-item>
                    </a-col>
                    <a-col :span="4">
                        <a-form-item label="容器号">
                            <a-input
                                placeholder="请输入容器号"
                                v-model="form.containerNo"
                            ></a-input>
                        </a-form-item>
                    </a-col>
                    <a-col :span="4">
                        <a-form-item label="工单号">
                            <a-input
                                placeholder="请输入工单号"
                                v-model="form.workOrderNo"
                            ></a-input>
                        </a-form-item>
                    </a-col>
                    <a-col :span="4">
                        <a-form-item :label="'\u00a0'">
                            <g-btn
                                text="查询"
                                class="mr-2"
                                type="primary"
                            ></g-btn>
                            <g-btn text="重置"></g-btn>
                        </a-form-item>
                    </a-col>
                </a-row>
            </a-form>
        </div>

        <div
            style="
                background-color: #fff;
                padding: 12px 24px 0;
                box-sizing: border-box;
                overflow-y: hidden;
                border-radius: 4px;
            "
        >
            <!-- <div
        style="display: flex; justify-content: flex-end; margin-bottom: 10px"
      >
        
      </div> -->
            <g-table-bar :spacing.sync="tableSize" :columns.sync="columns" :virtualColumns="virtualColumns" @refreshTable="refresh">
                <g-btn
                    text="添加追溯信息"
                    style="margin-right: 10px"
                    @callback="addOrEditInformation"
                ></g-btn>
                <g-btn
                    text="批量删除"
                    type="danger"
                    style="margin-right: 10px"
                ></g-btn>
                <g-btn text="导出excel" style="margin-right: 10px"></g-btn>
            </g-table-bar>
            <a-table
                id="tables"
                :columns="columns"
                :data-source="dataList"
                :scroll="{ y: tableheight }"
                :row-key="(record) => record.id"
                :pagination="pagination"
                :rowSelection="rowSelection"
                @change="tableChange"
                :loading="isTableLoading"
                :size="tableSize"
            >
                <!-- 状态 -->
                <span slot="status" slot-scope="text">
                    <a-tag color="blue">{{ text }}</a-tag>
                </span>
                <!-- 操作 -->
                <span slot="action" slot-scope="text, record">
                    <a
                        href="javascript:;"
                        class="mr-1"
                        @click="addOrEditInformation(record)"
                        >编辑</a
                    >
                    <a href="javascript:;" class="del" @click="delInformation(record)"
                        >删除</a
                    >
                </span>
            </a-table>
        </div>
    </div>
</template>

<script>
import { getTableScroll } from "@/utils/getTableScroll";
import createModal from "@/utils/modal";
import { tableBase } from "@/utils";
export default {
    name: "information-tracing-information",
    mixins: [tableBase],
    data() {
        return {
            tableheight: 0,
            form: {
                traceCode: "",
                productName: "",
                batchNo: "",
                containerNo: "",
                workOrderNo: "",
            },
        };
    },

    mounted() {
       

        this.columns = [
            // 序号 index
            {
                title: "序号",
                dataIndex: "id",
                key: "id",
                customRender: (text, record, index) => `${index + 1}`,
                width: 80,
            },
            {
                title: "追溯单号",
                dataIndex: "traceCode",
                key: "traceCode",
                ellipsis: true,
            },
            {
                title: "产品名称",
                dataIndex: "productName",
                key: "productName",
                ellipsis: true,
            },
            {
                title: "批次号",
                dataIndex: "batchNo",
                key: "batchNo",
                ellipsis: true,
            },
            {
                title: "容器号",
                dataIndex: "containerNo",
                key: "containerNo",
                ellipsis: true,
            },
            {
                title: "工单号",
                dataIndex: "workOrderNo",
                key: "workOrderNo",
                ellipsis: true,
            },
            {
                title: "追溯时间",
                dataIndex: "traceTime",
                key: "traceTime",
                // 排序
                sorter: true,
                ellipsis: true,
                width: 200,
            },

            {
                title: "操作",
                dataIndex: "action",
                key: "action",
                scopedSlots: { customRender: "action" },
            },
        ];
			//this.columns数组深拷贝this.virtualColumns
			this.virtualColumns = JSON.parse(JSON.stringify(this.columns)).map(
				(item) => {
					item.checked = true;
					return item;
				}
			)
			this.$nextTick(() => {
            this.tableheight = getTableScroll({ id: "tables" });
        });
		
    },

    methods: {
        addOrEditInformation(record) {
            createModal(
                {
                    title: record ? "编辑追溯信息" : "添加追溯信息",
                    width: "800px",
                },
                { component: CreateOrEditInformation } //  假设存在 CreateOrEditInformation 组件
            );
        },

        fetchData(request) {
            // 手动循环生成数据 200条
            this.isTableLoading = true;
            setTimeout(() => {
                var _data = [];
                for (let i = 0; i < 200; i++) {
                    _data[i] = {
                        id: i,
                        traceCode: "ZS" + i,
                        productName: "产品" + i,
                        batchNo: "PC" + i,
                        containerNo: "R" + i,
                        workOrderNo: "GZ" + i,
                        traceTime: "2023-01-01 00:00:00",
                    };
                }
                request.finishedCallback && request.finishedCallback();
                request.successCallback &&
                    request.successCallback({ items: _data, totalCount: 200 });
            }, 200);
        },

        delInformation(record) {
            // 删除 延时
            this.$confirm({
                title: "确认删除",
                content: "确认删除吗?",
                onOk: () => {
                    this.isTableLoading = true;
                    setTimeout(() => {
                        // 刷新
                        this.refresh();
                    }, 500);
                },
            });
        },
    },
};
</script>

<style lang="scss" scoped>
.flex-information-tracing-information {
    box-sizing: border-box;
}
</style>